@tailwind base;
@tailwind components;
@tailwind utilities;

html,
body {
  overflow-x: hidden;
}

img {
  cursor: pointer;
  max-width: 100%;
}

:focus {
  outline: none;
}

::-webkit-scrollbar {
  width: 20px;
}

::-webkit-scrollbar-track {
  background: #492F65;
}

::-webkit-scrollbar-thumb {
  background-color: #B0E212;
}

::-webkit-scrollbar-thumb:hover {
  cursor: pointer;
}

::selection {
  background-color: #B0E212;
  color: #FFFFFF;
}